.loader {
  width: 100%;
  height: 16px;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.is-hidden {
  display: none;
}

.loader:before, .loader:after {
  content: "";
  width: 16px;
  height: 16px;
  background: #000;
  border-radius: 50%;
  animation: ballMoveX 2s linear infinite;
  position: absolute;
  top: 20%;
  left: 50%;
  box-shadow: 32px 0 #000;
}

.loader:after {
  box-shadow: none;
  z-index: 2;
  animation: trfLoader 2s linear infinite;
  transform: translateX(64px)scale(1);
}

@keyframes trfLoader {
  0%, 5% {
    background: #000;
    transform: translateX(64px)scale(1);
  }

  10% {
    background: #ff3d00;
    transform: translateX(64px)scale(1);
  }

  40% {
    background: #ff3d00;
    transform: translateX(32px)scale(1.5);
  }

  90%, 95% {
    background: #ff3d00;
    transform: translateX(0)scale(1);
  }

  100% {
    background: #000;
    transform: translateX(0)scale(1);
  }
}

@keyframes ballMoveX {
  0%, 10% {
    transform: translateX(0);
  }

  90%, 100% {
    transform: translateX(32px);
  }
}

body {
  margin-left: 15%;
  margin-right: 15%;
}

.breed-select {
  margin-top: 20px;
  margin-bottom: 30px;
}

.cat-container {
  width: 100%;
  display: flex;
}

.cat-image {
  width: 50%;
}

.cat-details {
  width: 50%;
  padding-left: 20px;
}

